<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container">
      <div class="login">
        <div class="row">
          <div class="col-xl-5">
            <div class="log_img">
              <div class="log_img_title">{{$t('reg')}}</div>
              <img src="../../assets/images/llaptop.png" alt="">
            </div>
          </div>
          <div class="col-xl-7 ">
            <div class="log_form">
              <form v-if="!is_active&&guid==false" @submit.prevent="forgotPassword()">
                <div >
                  <label>Telefon raqam *</label>
                  <div class="input_text">
                    <input type="text" v-model="phone_number" placeholder="+998991234567">
                  </div>
                  <div class="log_btn mt-5">
                    <button>Verify Code</button>
                  </div>
                </div>
              </form>
              <form v-if="is_active" @submit.prevent="verifyResetPassword()">
                <div >
                  <label>{{$t('code')}} *</label>
                  <div class="input_text">
                    <input type="text" v-model="code" placeholder="Verification Code">
                  </div>
                  <div class="log_btn mt-5">
                    <button>Kodni tasdiqlash</button>
                  </div>
                </div>
              </form>
              <form v-if="this.guid!=false" @submit.prevent="registration()">
                <label>Yangi parol*</label>
                <div class="input_text">
                  <input  :type="passwordFieldType"
                          id="password"
                          v-model="password" :placeholder="$t('password')">
                  <a @click="togglePasswordVisibility" class="password_show" href="#">
                    <b-icon icon="eye" aria-hidden="true"></b-icon>
                  </a>
                </div>
                <label>Parolni takrorlang *</label>
                <div class="input_text">
                  <input  :type="passwordFieldType"
                          id="password"
                          v-model="confirm_password" :placeholder="$t('password')">
                  <a @click="togglePasswordVisibility" class="password_show" href="#">
                    <b-icon icon="eye" aria-hidden="true"></b-icon>
                  </a>
                </div>

                <div class="forgot_password">

                </div>
                <div class="log_btn">
                  <button>{{$t('login')}}</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";

export default {
  components: { Footer, Header },
  data() {
    return {
      email: '',
      phone_number: '',
      code: '',
      is_active:false,
      guid:false,
      password:'',
      confirm_password:''
    };
  },
  computed: {
    passwordFieldType() {
      return this.passwordVisible ? 'text' : 'password';
    }
  },

  methods: {
    togglePasswordVisibility() {
      this.passwordVisible = !this.passwordVisible;
    },
    forgotPassword() {
      // Create a FormData object
      const formData = new FormData();
      formData.append('phone_number', this.phone_number);

      // Send the POST request with axios
      this.$axiosDefault.post('/students/forgot-password/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
          .then(() => {
            // Show success toast notification
            this.is_active=true
            // Redirect to the login page

          })
          .catch(() => {
            // Show error toast notification
            this.$toast("Telefon raqam xato", {
              timeout: 1000,
              type: "error"
            });
            // Clear the email field (if needed)
            this.email = '';
          });
    },
    verifyResetPassword() {
      // Create a FormData object
      const formData = new FormData();
      formData.append('code', this.code);

      // Send the POST request with axios
      this.$axiosDefault.post('/students/verify-reset-password/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
          .then(res => {
            // Show success toast notification
            this.$toast("Kod tasdiqlandi", {
              timeout: 1000,
              type: "success"
            });
            this.guid=res.data.guid
          })
          .catch(() => {
            // Show error toast notification
            this.$toast("Kod xato", {
              timeout: 1000,
              type: "error"
            });
            // Clear the code field (if needed)
            this.code = '';
          });
    },
    registration() {
      const formData = new FormData();
      formData.append('password', this.password);
      formData.append('confirm_password', this.confirm_password);
      formData.append('guid', this.guid);

      this.$axiosDefault.post('/students/reset-password/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
          .then(() => {
            this.$toast("Parol yangilandi", {
              timeout: 1000,
              type: "success"
            });
            this.$router.push('/phone_log');
          })
          .catch(() => {
            this.$toast("Parol xato", {
              timeout: 1000,
              type: "error"
            });
            this.code = '';
          });
    }
  }
}
</script>

